// @flow
import { APP_BASE_URL, RIVERDALE_BASE_URL } from 'shared/Consts';
import type { BuildStep1Props, BuildStep1 } from 'types/Step';

export const buildStep1EndPoint = ({
  offerId,
  formId,
}: BuildStep1Props): BuildStep1 => {
  const formBaseEndpoint = `${RIVERDALE_BASE_URL}/formdata/forms/${formId}/items/${offerId}`;
  const redirectUrl = `${APP_BASE_URL}/offer?offerId=${offerId}&step=1&formId=${formId}`;

  return {
    formBaseEndpoint,
    formSubmissionEndpoint: `${formBaseEndpoint}/submit`,
    redirectUrl,
  };
};
