//@flow
import * as React from 'react';
import { buildStep1EndPoint } from 'helpers/Step';
import _get from 'lodash/get';

type Props = {
  index: string,
  offer: Object,
};

const Step1 = (props: Props): React.Element<'div'> => {
  const { offer } = props;

  const offerId = _get(offer, ['OfferId']);
  const formId = _get(offer, ['Forms', 0, 'FormId']);

  const endpoint = buildStep1EndPoint({ formId, offerId });

  return (
    <div className="offer_action">
      <a className="next_btn" href={endpoint.redirectUrl}>
        Continue Offer
      </a>
    </div>
  );
};

export default Step1;
